const meta: Meta<typeof ModalDocxContent> = {
  title: 'Components/Modal/ModalDocxContent',
  component: ModalDocxContent,
  tags: ['autodocs'],
  parameters: {
    componentSubtitle: `import { ModalDocxContent } from 'react-restyle-components'`,
    docs: {
      description: {
        component: `
A powerful document content editor modal with AI assistance and voice input capabilities.

## Features
- üìù Rich text editing with JoditEditor
- ü§ñ AI-powered content enhancement
- üé§ Voice input with auto-stop and continuous modes
- üé® Modern glass-morphism design
- ‚å®Ô∏è Keyboard shortcuts support (Esc to close)
- üé¨ Smooth animations and transitions
- üéØ Customizable styling with classNames and styles props
- ‚ôø Accessible with proper ARIA attributes

## Basic Usage
\`\`\`tsx
import { ModalDocxContent } from 'react-restyle-components';
import { useState } from 'react';

const [visible, setVisible] = useState(false);

<button onClick={() => setVisible(true)}>Open Editor</button>
<ModalDocxContent
  visible={visible}
  details=""
  title="Document Editor"
  onSave={(content) => {
    console.log('Saved:', content);
    setVisible(false);
  }}
  onClose={() => setVisible(false)}
/>
\`\`\`

## Voice Recording Modes
- **Auto-Stop Mode (Green)**: Automatically stops recording after 5 seconds of silence
- **Continuous Mode (Purple/Red)**: Records continuously until manually stopped

## AI Enhancement
Select text in the editor and click the AI button to enhance the selected content, or click without selection to process the entire document.
        `,
      },
    },
    layout: 'centered',
  },
  decorators: [
    (Story) => (
      <Wrapper>
        <Story />
      </Wrapper>
    ),
  ],
};

export default meta;
