export const CustomAIService: Story = {
  name: 'Custom AI Service',
  render: () => {
    const [visible, setVisible] = useState(false);

    // Custom AI service that transforms text
    const customAIService = async (message: string) => {
      // Simulate API delay
      await new Promise((resolve) => setTimeout(resolve, 1500));
      return {
        text: `‚ú® Enhanced: ${message.toUpperCase()} ‚ú®`,
      };
    };

    return (
      <>
        <StyledButton className="primary" onClick={() => setVisible(true)}>
          Open with Custom AI
        </StyledButton>
        <ModalDocxContent
          visible={visible}
          title="AI-Powered Editor"
          aiService={customAIService}
          details="<p>Type some text and select it, then click the AI button to transform it!</p>"
          onSave={(content) => {
            console.log('Content saved:', content);
            setVisible(false);
          }}
          onClose={() => setVisible(false)}
        />
      </>
    );
  },
  parameters: {
    docs: {
      source: {
        code: `
const customAIService = async (message: string) => {
  // Your custom AI integration
  const response = await fetch('/api/ai', {
    method: 'POST',
    body: JSON.stringify({ text: message }),
  });
  return response.json();
};

<ModalDocxContent
  visible={visible}
  title="AI-Powered Editor"
  aiService={customAIService}
  onSave={(content) => setVisible(false)}
  onClose={() => setVisible(false)}
/>
        `,
        language: 'tsx',
      },
      description: {
        story:
          'Editor with a custom AI service for content enhancement. The AI button uses the provided service function instead of the default.',
      },
    },
  },
};

